.cards {
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
    flex-direction: column;
    gap: 2.6rem;
    margin-left: auto;
    margin-bottom: 10%;
}
.card{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fbfbfb;
    padding: 1.9rem 4.5rem 1.9rem 4.5rem;
    width:auto;
    position: relative;
    left: 20%;
    border-radius: 10px;
    gap: 2rem;
}
.img-card{
    width: 7rem;
}
.container-number-type{
    display: flex;
    flex-direction: column;
}
.indication-number{
    font-weight: bold;
    font-size: 1.2rem;
}
.indication-type{
    opacity: 0.8;
}
@media(min-width: 1024px) and (max-width: 1600px) {
    .img-card{
        width: 3rem;
    }
    .cards{
        gap: 1rem;
        flex-direction: row;
        position: inherit;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 95%;
        padding-right: 0rem;
    }

    .indication-number{
        font-size: 1rem;
        padding-right: 3rem;
    }
    .indication-type{
        font-size: 0.9rem;
        padding-right: 3rem;
    }
    .card{
        position: inherit;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 1rem 0.5rem 1rem 0.5rem;
        width: auto;
    }
    .containerInfosRight{
        margin-top: 5%;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}